<template>
  <div class="mulo-turntable">
    <div
      class="turntable-main"
      :style="{transform:'rotate('+rotateDeg+'deg)','background-color':bgc,transition:'all '+this.duration+'s'}"
    >
      <div class="turntable-award">
        <div
          class="turntable-award-item"
          v-for="(li,index) in rules"
          :key="index"
          :style="{transform:'rotate('+position(index)+'deg)',color:'red'}"
        >
          <div
            class="award-item-bgline"
            :class="{'award-item-bgline-big':itemLinePosition(index)>180,'award-item-bgline-180':itemLinePosition(index)==180}"
            :style="   {transform:'rotate('+itemLinePosition(index)/2+'deg) '  +( itemLinePosition(index)<180?'skewY('+(90-itemLinePosition(index) )+'deg)':'' ) +' scale(30)','background-color':(li.backgroundColor) }"
          ></div>
          <div class="award-info">
            <div class="award-title">{{li.title}}</div>
            <img class="award-img" :src="li.img" alt="å¥å" srcset />
          </div>
        </div>
      </div>
      <div class="turntable-bg"></div>
    </div>
    <div @click="clickBegin" class="turntable-start-btn">
      <div class="turntable-start-btn-text">Go!</div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "mulo-turntable",
  created() {},
  data() {
    return {
      //éè¿æ¦çè®¡ç®ç»æçæ¦çå­æ®µ
      probabilityField: "probability",
      //è½¬çæè½¬çåº¦æ° deg
      rotateDeg: 0,
      awardIndex: 0
    };
  },
  computed: {
    totalRatio() {
      let ratio = 0;
      this.rules.forEach((li, index) => {
        ratio += li.ratio;
      });
      return ratio;
    }
  },
  props: {
    ratioField: {
      type: String,
      default: "ratio"
    },
    //identical  ä¸æ · ratioåéæ¾ç¤ºå¤§å°
    ruleSizeType: {
      type: String,
      default: "ratio"
    },
    //åºç¡èæ¯è² ç¨äºå­å¨è¶è¿180åº¦çé»è®¤å
    bgc: {
      type: String,
      default: "green"
    },
    //æ¾ç¤ºèæ¯è²å
    showBg: {
      type: Boolean,
      default: true
    },
    //æéæååº¦æ°
    pointDeg: {
      type: Number,
      default: 90
    },
    duration: {
      type: Number,
      default: 5
    },
    rules: {
      type: Array,
      default: () => {
        return [
          {
            title: "ä¸ç­å¥",
            img: "/xiaolian.png",
            ratio: 5,
            backgroundColor: "blue"
          },
          {
            title: "äºç­å¥",
            img: "/xiaolian.png",
            ratio: 15
          },
          {
            title: "ä¸ç­å¥",
            img: "/xiaolian.png",
            ratio: 80,
            backgroundColor: "pink"
          }
        ];
      }
    }
  },
  methods: {
    /**
     * å¼å§è½¬ç
     *
     * @param {Int | Bool<false>} index é¡¹ç®åè¡¨ rulesçindex
     *
     *
     */
    begin(index) {
      this.awardIndex = index;
      let rule = this.rules[index];
      let position = this.position(index);
      //è½¬å°æ¹ååå¼æ¯ç¸åç
      position = 360 - position;

      let rotateDeg =
        this.rotateDeg + 360 * 10 + position + (360 - (this.rotateDeg % 360));
      //éæºåç§»è§åº¦
      let size = this.itemLinePosition(index);
      if ((size - 4) / 2 >= 2) {
        //åç¦»å¼
        let offsetValue = this.randomNum(0, (size - 4) / 2);
        let isNegativeNumber = this.randomNum(0, 1);
        if (isNegativeNumber == 1) {
          offsetValue = 0 - offsetValue;
        }
        rotateDeg += offsetValue;
      }
      this.rotateDeg = rotateDeg;
      setTimeout(() => {
        this.end();
      }, this.duration * 1000);
    },
    /**
     *
     */
    end() {
      this.$emit("end", {
        rotateDeg: this.rotateDeg,
        awardIndex: this.awardIndex
      });
    },
    /**
     * å¼å§æé®ç¹å»äºä»¶
     * ç¹å»å¼å§å¼å§æé®,äº¤ç»
     *
     */
    clickBegin() {
      this.$emit("begin", {
        begin: this.begin
      });
    },
    //çæä»minNumå°maxNumçéæºæ°
    randomNum(minNum, maxNum) {
      switch (arguments.length) {
        case 1:
          return parseInt(Math.random() * minNum + 1, 10);
          break;
        case 2:
          return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
          break;
        default:
          return 0;
          break;
      }
    },
    /**
     * è®¡ç®åç§»è§åº¦
     */
    position(index) {
      //å¹³åå¤§å°
      if (this.ruleSizeType == "identical") {
        return this.getIdentical(index);
      }
      //æç§æ¯çå¼åéå¤§å°
      if (this.ruleSizeType == "ratio") {
        return this.lastRatio(index);
      }
    },
    /**
     * åé¢çè§åº¦å ç¨
     * éè¿æ¯çè®¾ç½®è§åº¦, ç¬¬ä¸ä¸ªæ»æ¯å¹³çææ¾, å¶ä»çèå»¶
     *
     * @return {BeforeDeg} åé¢çåº¦æ°
     */
    lastRatio($index) {
      let deg = 0;
      for (let index = 0; index < $index; index++) {
        let ruleDeg =
          360 * (this.rules[index][this.ratioField] / this.totalRatio);

        deg += index == 0 ? ruleDeg : ruleDeg;
      }
      //å½åååæå åº¦æ°
      return deg + this.itemLinePosition($index) / 2;
    },
    /**
     * ä¸ä¸ªè§åçåç§»åº¦
     *
     */
    getIdentical(index) {
      let num = this.rules.length;
      if (!num) {
        return 0 + this.itemLinePosition(index) / 2;
      }
      let one = 360 / num;
      return index * one + this.itemLinePosition(index) / 2;
    },
    /**
     * åç´ å ç¨çå¼§åº¦
     *
     */
    itemLinePosition(index) {
      if (this.ruleSizeType == "ratio") {
        return 360 * (this.rules[index][this.ratioField] / this.totalRatio);
      }

      let num = this.rules.length;
      if (!num) {
        return 0;
      }
      let one = 360 / num;
      return one;
    }
  }
};
</script>

<style lang="scss">
.mulo-turntable {
  width: 16rem;
  height: 16rem;
  position: relative;
  //è½¬çä¸»ä½
  .turntable-main {
    background-color: goldenrod;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    transition: all 1s;
    transition-timing-function: ease;
    > div {
      width: 100%;
      height: 100%;
      position: relative;
    }
    .turntable-award {
      // å¥å
      .turntable-award-item {
        width: 100%;
        height: 50%;
        text-align: center;
        left: 0;
        top: 0;
        position: absolute;
        transform-origin: center bottom;
        &:nth-last-child(2n) {
          .award-item-bgline {
            background: pink;
          }
        }
      }
      //æ é¢
      .award-title {
        margin-top: 0.7rem;
        text-align: center;
        color: black;
      }
      .award-img {
        width: 1rem;
        height: 1rem;
        margin-top: 0.7rem;
      }
      .award-item-bgline {
        &.award-item-bgline-big {
          background-color: unset;
        }
        //åæ¶skewè®©ä»åæé¿æ¹å½¢å ä½
        &.award-item-bgline-180 {
          margin-left: -100%;
        }

        top: 0;
        left: 0;
        margin-left: -50%;
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        background: green;
        content: "";
        transform-origin: right bottom;
      }
      .award-info {
        position: absolute;
        top: 0;
        left: 0%;
        width: 100%;
        text-align: center;
        top: 0;
      }
    }
  }
  $btnColor: yellow;
  .turntable-start-btn {
    border-radius: 50%;
    position: absolute;
    bottom: 50%;
    right: 50%;
    transform: translate(50%, 50%);
    background-color: $btnColor;
    text-align: center;
    width: 3rem;

    height: 3rem;
    &::before {
      content: "";
      position: absolute;
      top: -1rem;
      left: 50%;
      width: 0;
      height: 0;
      border-left: 1rem solid transparent;
      border-right: 1rem solid transparent;
      border-bottom: 3rem solid $btnColor;
      transform: translate(-50%);
    }
  }
  .turntable-start-btn-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>